function LinearProgressWithLabel(props) {
    return (
        <Box display="flex" alignItems="center">
            <Box width="100%" mr={1}>
                <LinearProgress variant="determinate" {...props} />
            </Box>
            <Box minWidth={35}>
                <Typography variant="body2" color="textSecondary">{`${Math.round(
                    props.value,
                )}%`}</Typography>
            </Box>
        </Box>
    );
}

LinearProgressWithLabel.propTypes = {
    /**
     * The value of the progress indicator for the determinate and buffer variants.
     * Value between 0 and 100.
     */
    value: PropTypes.number.isRequired,
};

const initialTodoState = {
    name: '',
    checked: false,
    id: null
}

class Hooks extends Component {

    constructor(props) {
        super(props);
        this.state = {
            count: 1,
            name: '',
            todos: [],
            todo: {
                name: '',
                checked: false,
                id: null,
                // editMode: false
            },
            doneTodos: 0
        };
    }


    componentDidMount() {
        axios.get(`https://floating-gorge-87832.herokuapp.com/todos`)
            .then(res => {
                const todos = res.data.data.reverse();
                const doneTodos = [...todos].filter(todo => todo.checked === true).length
                this.setState({ todos, doneTodos });
            })
            .catch(err => {
                console.log(err)
            })
    }

    handleTodoInput = (e) => {
        this.setState({
            todo: {
                ...this.state.todo,
                name: e.target.value
            }
        })
    }

    handleSubmitTodo = () => {
        if (this.state.todo.name !== '') {
            var todo = {
                name: this.state.todo.name,
                checked: false,
                editMode: false,
                id: uuid()
            }
            axios.post(`https://floating-gorge-87832.herokuapp.com/todos`, { todo })
                .then(res => {
                    axios.get(`https://floating-gorge-87832.herokuapp.com/todos`)
                        .then(res => {
                            const todos = res.data.data.reverse();
                            console.log('all todos', todos)
                            this.setState({ todos });
                        })
                        .catch(err => {
                            console.log(err)
                        })
                    this.setState({
                        todo: initialTodoState
                    })
                    console.log('post add', res)
                })
        }
    }


    handleCheckboxAndProcess(e, id) {
        const handleTodoCheckbox = (e, id) => {
            const todos = [...this.state.todos];
            const index = todos.findIndex(todo => todo.id === id);
            const todo = todos[index]
            todo.checked = !todo.checked;

            axios.post(`https://floating-gorge-87832.herokuapp.com/todos/update/${todo.id}`, { todo })
                .then(res => {
                    axios.get(`https://floating-gorge-87832.herokuapp.com/todos`)
                        .then(res => {
                            const todos = res.data.data.reverse();
                            const doneTodos = [...todos].filter(todo => todo.checked === true).length
                            this.setState({ todos, doneTodos });
                        })
                        .catch(err => {
                            console.log(err)
                        })
                })
        };

        handleTodoCheckbox(e, id);
    }

    handleEditMode(e, id) {
        const todos = [...this.state.todos];
        const index = todos.findIndex(todo => todo.id === id);
        todos[index].editMode = !todos[index].editMode;

        this.setState({
            todos: todos
        });
    }

    handleEditInput = (e, id) => {
        const todos = [...this.state.todos];
        const index = todos.findIndex(todo => todo.id === id);
        todos[index].name = e.target.value;

        this.setState({
            todos
        })
    }

    handleEditSubmit(e, id) {
        const todos = [...this.state.todos];
        const index = todos.findIndex(todo => todo.id === id);
        const todo = todos[index]
        todo.editMode = false;

        axios.post(`https://floating-gorge-87832.herokuapp.com/todos/update/${todo.id}`, { todo })
            .then(res => {
                axios.get(`https://floating-gorge-87832.herokuapp.com/todos`)
                    .then(res => {
                        const todos = res.data.data.reverse();
                        const doneTodos = [...todos].filter(todo => todo.checked === true).length
                        this.setState({ todos, doneTodos });
                    })
                    .catch(err => {
                        console.log(err)
                    })
            })
    }

    handleDeleteTodo(e, id) {
        const todos = [...this.state.todos];
        const index = todos.findIndex(todo => todo.id === id);

        axios.post(`https://floating-gorge-87832.herokuapp.com/todos/delete/${todos[index].id}`)
            .then(res => {
                axios.get(`https://floating-gorge-87832.herokuapp.com/todos`)
                    .then(res => {
                        const todos = res.data.data.reverse();
                        const doneTodos = [...todos].filter(todo => todo.checked === true).length
                        this.setState({ todos, doneTodos });
                    })
                    .catch(err => {
                        console.log(err)
                    })
            })
    }

    render() {
        return (

            <div>
                <Container maxWidth="sm">
                    <CssBaseline />
                    <Typography style={{ padding: '16px 8px' }} component="h2" align='center' color='primary'>TASK MANAGER APP</Typography>
                    <Typography component="body1" align='left' color='primary'>Your productivity</Typography>
                    <LinearProgressWithLabel style={{ margin: '1em 0' }} value={(this.state.doneTodos / this.state.todos.length) * 100} />

                    <Box
                        display='flex'
                        alignItems='center'
                        justifyContent='center'
                        mt='1em'
                    >
                        <TextField style={{ flexGrow: 1, marginRight: '16px' }} id="outlined-basic" value={this.state.todo.name} onChange={this.handleTodoInput} label="Set daily tasks and decipline yourself" variant="outlined" />
                        <IconButton onClick={this.handleSubmitTodo} color='primary' >
                            <FaPlus />
                        </IconButton>
                        {/* <button onClick={this.handleSubmitTodo}>Add todo</button> */}
                    </Box>
                    <Box
                        py='2em'
                    >
                        <List>
                            {
                                this.state.todos.map((todo, i) => {
                                    return (
                                        <ListItem key={todo.id}>
                                            <Checkbox
                                                checked={todo.checked}
                                                onChange={(event) => this.handleCheckboxAndProcess(event, todo.id)}
                                                name="checkedB"
                                                color="primary"
                                            />
                                            <Box p='5px 10px'>
                                                <ListItemText
                                                    primary={!todo.editMode ? todo.name : <TextField value={todo.name} id="standard-basic" label="Standard" onChange={event => this.handleEditInput(event, todo.id)} />}
                                                />
                                            </Box>
                                            <ListItemSecondaryAction>
                                                {!todo.editMode ?
                                                    (<>
                                                        <IconButton onClick={event => this.handleEditMode(event, todo.id)} color='primary' edge="end" aria-label="edit">
                                                            <MdEdit />
                                                        </IconButton>
                                                        <IconButton onClick={event => this.handleDeleteTodo(event, todo.id)} color='secondary' edge="end" aria-label="delete">
                                                            <MdDelete />
                                                        </IconButton>
                                                    </>)
                                                    : (
                                                        <Button onClick={event => this.handleEditSubmit(event, todo.id)} variant="contained" color="primary">
                                                            Save
                                                        </Button>
                                                    )
                                                }
                                            </ListItemSecondaryAction>
                                        </ListItem>
                                    )
                                })
                            }
                        </List>
                    </Box>
                </Container>
            </div >
        );
    };
}

